import PropTypes from 'prop-types';
import { format } from 'date-fns';
import {
  Box,
  Chip,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography
} from '@mui/material';

export const OrderListResults = ({ orders, ...rest }) => {
  return (
    <>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>ID</TableCell>
            <TableCell>Date</TableCell>
            <TableCell>Total</TableCell>
            <TableCell>Status</TableCell>
            {/* <TableCell>
                 Actions
                </TableCell> */}
          </TableRow>
        </TableHead>
        <TableBody>
          {orders.map((order) => (
            <TableRow
              hover
              key={order.id}
              // selected={selectedCustomerIds.indexOf(order.id) !== -1}
            >
              <TableCell>
                <Box
                  sx={{
                    alignItems: 'center',
                    display: 'flex'
                  }}
                >
                  <Typography color="textPrimary" variant="body1">
                    {order.id?.substr(0, 8)?.toUpperCase()}
                  </Typography>
                </Box>
              </TableCell>
              <TableCell>
                {format(Number(order.createdAt), 'dd/MM/yyyy')}
              </TableCell>

              <TableCell>${order.totalAmount}</TableCell>
              <TableCell>
                {/* ['Placed', 'Processed','Delivered','Complete'] */}
                {order.status === 'Placed' && (
                  <Chip label={order.status} sx={{ bgcolor: 'success.main' }} />
                )}
                {order.status === 'Processed' && (
                  <Chip label={order.status} sx={{ bgcolor: 'info.main' }} />
                )}
                {order.status === 'Delivered' && (
                  <Chip label={order.status} sx={{ bgcolor: 'grey.400' }} />
                )}
                {order.status === 'Complete' && (
                  <Chip label={order.status} sx={{ bgcolor: 'error.main' }} />
                )}
              </TableCell>
              {/* <TableCell>
                     <IconButton >
                      <Details />
                     </IconButton>
                     <IconButton >
                      <VoicemailRounded />
                     </IconButton>
                  </TableCell> */}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </>
  );
};

OrderListResults.propTypes = {
  orders: PropTypes.array.isRequired
};
