import React from 'react';

// @mui
import { Box, Stack, Typography, useTheme } from '@mui/material';

// third-party
import Chart from 'react-apexcharts';

export default function ChartCard(props) {
  const { title, value, options, icon, color } = props;
  const theme = useTheme();

  return (
    <Box
      sx={{
        width: '100%',
        maxWidth: 580,
        height: 136,
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
        p: 2,
        boxShadow: '0px 1px 15px 1px rgba(69, 65, 78, 0.08)',
        borderRadius: `${theme.shape.borderRadius}px` || 2,
        backgroundColor: color
          ? theme.palette[color].main
          : theme.palette.primary.main, //'#2B2D3E',
        position: 'relative'
      }}
    >
      <Box
        sx={{
          display: 'flex',
          height: '100%',
          flexDirection: 'column',
          justifyContent: 'space-between',
          alignItems: 'flex-start',
          color: theme.palette.common.white,
          transform: 'scale(0.7) translate(-22px, 20px)'
        }}
      >
        <Typography variant="h1" fontWeight={500}>
          {value}
        </Typography>
        <Stack direction="row" spacing={1} alignItems={'center'}>
          {icon}
          <Typography variant="h3" gutterBottom>
            {title}
          </Typography>
        </Stack>
      </Box>
      <Box sx={{ position: 'relative', left: -10 }}>
        <Chart
          options={options}
          series={options.series}
          width="100%"
          height={100}
        />
      </Box>
    </Box>
  );
}
